import Bridge from "suplink-sdk/bridge";
import { Return, Params } from "./interface";

export default function filePreview(params: Params): Promise<Return> {
  let { url, title = "" } = params;
  if (!url) {
    throw new Error("");
  }
  return Bridge.dispatchAppEvent("sl.multiMedia.preview", {
    previewUrl: url,
    sourceFileUrl: url,
    title,
  });
}
